body{
  overflow-y:scroll;  /* make scrollbar visible*/
  overflow-x:hidden;
}

/*pretty much same things, just covering all bases*/
:root, body, html {
  background-color: #f5f5f5;
  box-sizing: border-box; 
  
}


div, h1, h2, h3, h4, h5, h6, header, html, i, img, label, li, nav, p, small, span, ul {
margin: 0;
padding: 0;
border: 0;
outline: none;
font-size: 100%;
vertical-align: baseline;
}

body {
  font-family : Arial, sans-serif;
}